import { Controller, Commands, Block } from '@ali/4ever-cangjie';
import { biActions as basicActions } from '@ali/4ever-plugin-basic';
import { FOCUSED_CODE } from '../constants/value';

export default function removeCode(controller: Controller, key: string) {
  controller.run('onAction', basicActions.createMergeDataAction({ [FOCUSED_CODE]: null }));

  return controller.command(Commands.replaceNodeByKey, key, Block.create('paragraph'));
}
